import React from "react";
import { Switch, Redirect, Route } from "react-router-dom";
import loadable from "@loadable/component";
import Nav from "@/components/Common/Nav";
import Footer from "@/components/Common/Footer";
import Home from "@/pages/Home";
import a from './App.less';

const Battle = loadable(() => import("@/pages/Battle"));
const Result = loadable(() => import("@/pages/Result"));

class App extends React.Component {

  render() {
    return (
      <div className={a.app}>
        <Nav />
        <Switch>
          <Redirect exact from="/" to="/popular" />
          <Route path="/popular" component={Home} />
          <Route exact path="/battle" component={Battle} />
          <Route path="/battle/result" component={Result} />
        </Switch>
        <Footer />
      </div>
    );
  }
}
export default App;
