// 定义头部
import React from "react";
import header from "./Header.less";

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: ["All", "JavaScript", "Ruby", "Java", "CSS", "Python"],
      param: null
    };
  }

  componentDidMount() {
    const s = this.props.props.location.search;
    const p = (new URLSearchParams(s)).get('language');
    this.setState({ param: p });
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    const s = newProps.props.location.search;
    const p = (new URLSearchParams(s)).get('language');
    this.setState({ param: p });
  }

  render() {
    const { title, param } = this.state;
    const { onClick } = this.props;
    console.log("param",param)
    return (
      <ul className={header.header}>
        {title.map((item,index) => (
          <li
            style={{ color: (param===item.toLowerCase()||(param===null&&index===0))?'#b42c1e':'#000'}}
            key={item}
            onClick={() => onClick(item.toLowerCase())}>
            {item}
          </li>
        ))}
      </ul>
    );
  }
}
export default Header;
